import { Injectable } from '@angular/core';

interface ValidationResult {
  [validator: string]: string | boolean;
}

@Injectable({
  providedIn: 'root'
})
export class ValidateMesssageService {
  getMessage(validator: ValidationResult, key: string): string {
    switch (key) {
      case 'required':
        return 'Please enter a value';
      case 'requiredTrue':
        return 'Please set required selection';
      case 'pattern':
        return 'Value does not match required pattern';
      case 'minlength':
        return `Value must be a minimum of ${(validator as any).minlength.requiredLength} characters`;
      case 'maxlength':
        return `Value must be a maximum of ${(validator as any).maxlength.requiredLength} characters`;
    }

    if (typeof validator[key] === 'string') {
      return validator[key] as string;
    } else {
      return `Validation failed: ${key}`;
    }
  }
}
