import {
  forwardRef,
  Input,
  ElementRef,
  ViewChild,
  OnInit,
  HostBinding,
  HostListener,
  Output,
  EventEmitter
} from '@angular/core';
import {
  AsyncValidatorFn,
  ControlContainer,
  ControlValueAccessor,
  FormControl,
  NG_VALUE_ACCESSOR,
  NgForm,
  ValidationErrors,
  ValidatorFn
} from '@angular/forms';
import { distinctUntilChanged } from 'rxjs/operators';

export abstract class CustomInputComponentAbstract implements ControlValueAccessor, OnInit {
  protected _value = '';
  protected _disabled = false;
  protected _touched = false;
  protected isNgForm: boolean;
  protected formControl: FormControl;

  @ViewChild('inputRef', { read: ElementRef }) inputElementRef: ElementRef;
  @HostBinding('class') classList = '';

  @Input() class: string;
  @Input() name: string;
  @Input() inputId: string;
  @Input() formControlName: string;

  @Input()
  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: boolean) {
    // allow setting an empty value as true
    value = value !== false;

    if (value !== this._disabled && this.inputElementRef && this.formControl) {
      this._disabled = value;
      this.inputElementRef.nativeElement.disabled = value;
      this._disabled ? this.formControl.disable() : this.formControl.enable();
      this.onDisabledChange(value);
    }
  }

  @Input()
  get value(): any {
    return this._value;
  }

  set value(v: any) {
    if (v !== this._value) {
      this._value = v;
      this.onChange(v);
    }
  }

  @Output() focusOut: EventEmitter<boolean> = new EventEmitter<false>();

  @HostListener('focusout')
  private onFocusOut(): void {
    this.onTouched();
    this._touched = true;
    this.focusOut.emit(true);
  }

  get errors(): ValidationErrors {
    return this.formControl.errors;
  }

  get touched(): boolean {
    return this._touched;
  }

  get control(): FormControl {
    return this.formControl;
  }

  protected constructor(
    validators: ValidatorFn | ValidatorFn[] = null,
    asyncValidators: AsyncValidatorFn | AsyncValidatorFn[] = null,
    private readonly controlContainer: ControlContainer
  ) {
    this.isNgForm = this.controlContainer && this.controlContainer.formDirective instanceof NgForm;

    // usage inside a template-driven form
    if (this.isNgForm) {
      this.formControl = new FormControl();
      this.formControl.setValidators(validators);
      this.formControl.setAsyncValidators(asyncValidators);
    }
  }

  ngOnInit(): void {
    // reactive form usage, so get the formControl from ControlContainer instance
    if (!this.formControl) {
      this.formControl =
        (this.controlContainer && (this.controlContainer.control.get(this.formControlName) as FormControl)) ||
        new FormControl();
    }

    this.formControl.valueChanges.pipe(distinctUntilChanged()).subscribe(value => {
      this.value = value;
    });

    if (!this.inputId) {
      this.inputId = this.name;
    }
    if (this.class) {
      this.classList += ` ${this.class}`;
    }
  }

  /**
   * Invoked when the model has been changed
   */
  onChange: (_: any) => void = (_: any) => {};

  /**
   * Invoked when the model has been touched
   */
  onTouched: () => void = () => {};

  /**
   * Invoked when `disabled` property has been changed
   */
  onDisabledChange: (_: any) => void = (_: any) => {};

  ///////////////
  // OVERRIDES //
  ///////////////

  /**
   * Writes a new item to the element.
   * @param value the value
   */
  writeValue(value: any): void {
    if (value) {
      this._value = value;
      if (this.isNgForm) {
        this.formControl.setValue(value);
      }
    }
  }

  /**
   * Registers a callback function that should be called when the control's value changes in the UI.
   * @param fn
   */
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  /**
   * Registers a callback function that should be called when the control receives a blur event.
   * @param fn
   */
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  registerOnDisabledChange(fn: any): void {
    this.onDisabledChange = fn;
  }
}

// helper to create providers from custom input component
export function MakeProviders(type: any) {
  return [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => type),
      multi: true
    },
    {
      provide: CustomInputComponentAbstract,
      useExisting: forwardRef(() => type),
      multi: true
    }
  ];
}
