import { AbstractControl, ValidationErrors } from '@angular/forms';

export class CustomValidators {
  // replacement for already existing requiredTrue validator, as this one has a specific error key requiredTrue
  static requiredTrue(control: AbstractControl): { [key: string]: any } {
    const error: ValidationErrors = { requiredTrue: true };
    if (!control.value) {
      return error;
    } else {
      return null;
    }
  }
}
