import { Directive, forwardRef } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, Validator } from '@angular/forms';
import { CustomValidators } from './custom-validators';

@Directive({
  selector: '[uiCoreRequiredTrue][formControl],[uiCoreRequiredTrue][ngModel]',
  providers: [{ provide: NG_VALIDATORS, useExisting: forwardRef(() => RequiredTrueDirective), multi: true }]
})
export class RequiredTrueDirective implements Validator {
  validate(control: AbstractControl): { [key: string]: any } {
    return CustomValidators.requiredTrue(control);
  }
}
