import { Component, Input, HostBinding, Inject, Optional, Host } from '@angular/core';
import { CustomInputComponentAbstract, MakeProviders } from './custom-input-component.abstract';
import { ControlContainer, NG_ASYNC_VALIDATORS, NG_VALIDATORS } from '@angular/forms';

@Component({
  selector: 'ui-core-input-field',
  templateUrl: './input-field.component.html',
  styleUrls: ['./input-field.component.scss'],
  providers: MakeProviders(InputFieldComponent)
})
export class InputFieldComponent extends CustomInputComponentAbstract {
  @Input() placeholder = '';
  @Input() screenReaderLabel: string;
  @Input() useScreenReaderLabel = false;
  @HostBinding('class') classList = 'ui-core-input-field';
  @HostBinding('class.is-disabled') isDisabled = false;

  constructor(
    @Optional() @Inject(NG_VALIDATORS) validators: Array<any>,
    @Optional() @Inject(NG_ASYNC_VALIDATORS) asyncValidators: Array<any>,
    @Optional() @Host() controlContainer: ControlContainer
  ) {
    super(validators, asyncValidators, controlContainer);
  }
}
