import { Component, OnInit, Input, HostBinding, ViewChild } from '@angular/core';
import { InputFieldComponent } from '../form/input-field.component';

@Component({
  selector: 'ui-core-search-field',
  templateUrl: './search-field.component.html',
  styleUrls: ['./search-field.component.scss']
})
export class SearchFieldComponent implements OnInit {
  @Input() class = '';
  @Input() name: string;
  @Input() inputId: string;
  @Input() placeholder = '';
  @Input() disabled = false;
  @Input() headerVariant = false;
  @Input() buttonTitle = 'Click to search';
  @HostBinding('class') classList = 'ui-core-search-field';
  @ViewChild('inputField') inpField: InputFieldComponent;

  constructor() {}

  ngOnInit() {
    if (this.class) {
      this.classList += ` ${this.class}`;
    }

    if (this.headerVariant) {
      this.classList += ' header-variant';
    }

    this.inpField.registerOnDisabledChange(isDisabled => {
      this.inpField.isDisabled = isDisabled;
    });
  }
}
