import { AfterViewInit, ChangeDetectorRef, Component, Host, Inject, Input, Optional } from '@angular/core';
import { CustomInputComponentAbstract, MakeProviders } from './custom-input-component.abstract';
import { ControlContainer, NG_ASYNC_VALIDATORS, NG_VALIDATORS } from '@angular/forms';

@Component({
  selector: 'ui-core-textarea',
  templateUrl: './textarea.component.html',
  providers: MakeProviders(TextareaComponent)
})
export class TextareaComponent extends CustomInputComponentAbstract implements AfterViewInit {
  @Input() placeholder = '';
  @Input() rows = 1;
  @Input() maxlength: number;
  @Input() labelCharsLeft: string;
  @Input() resizable = false;
  @Input() expandOnFocus = false;
  @Input() screenReaderLabel: string;
  @Input() useScreenReaderLabel = false;

  labelRemaining: string;
  height: number;
  collapseHeight: number;
  expandHeight: number;

  get styleHeight() {
    return this.height
      ? {
          height: `${this.height}px`
        }
      : {};
  }

  constructor(
    @Optional() @Inject(NG_VALIDATORS) validators: Array<any>,
    @Optional() @Inject(NG_ASYNC_VALIDATORS) asyncValidators: Array<any>,
    @Optional() @Host() controlContainer: ControlContainer,
    private readonly cdr: ChangeDetectorRef
  ) {
    super(validators, asyncValidators, controlContainer);
  }

  ngAfterViewInit() {
    this.calculateExpandHeight();

    if (this.labelCharsLeft && this.maxlength > 0) {
      this.formControl.valueChanges.subscribe(value => {
        this.calculateCharsLeft(value);
      });

      this.calculateCharsLeft(this.value);
      this.cdr.detectChanges();
    }
  }

  /**
   * calculate expanded textarea height based on row count applied
   */
  calculateExpandHeight() {
    if (this.expandOnFocus) {
      const el = this.inputElementRef.nativeElement;
      el.rows = 1;
      const elHeight = el.offsetHeight;
      el.rows = this.rows;

      this.expandHeight = el.offsetHeight;
      this.collapseHeight = this.height = elHeight;
    }
  }

  /**
   * caclulates how many chars left if textarea has a maxlength restriction
   */
  calculateCharsLeft(value: string) {
    const actLength = value ? value.length : 0,
      remaining = this.maxlength - actLength;
    this.labelRemaining = this.labelCharsLeft.replace('{0}', remaining.toString());
  }
}
