import {
  AfterContentInit,
  Component,
  ContentChildren,
  Input,
  QueryList,
  HostBinding,
  Inject,
  OnDestroy,
  Optional
} from '@angular/core';
import { AbstractControl, ControlContainer, FormControl, NgForm } from '@angular/forms';
import { CustomInputComponentAbstract } from './custom-input-component.abstract';
import { ValidateMesssageService } from './validation';
import { InputFieldComponent } from './input-field.component';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';
import { merge, Subject } from 'rxjs';
import { TextareaComponent } from './textarea.component';

@Component({
  selector: 'ui-core-form-element',
  templateUrl: './form-element.component.html'
})
export class FormElementComponent implements AfterContentInit, OnDestroy {
  @ContentChildren(CustomInputComponentAbstract) private readonly inputComponentList: QueryList<
    CustomInputComponentAbstract
  >;

  @Input() label: string;
  @Input() labelColumnClasses: string;
  @Input() inputColumnClasses: string;

  @HostBinding('class') classList = 'w-100';

  labelForId: string;
  useFieldset: boolean;
  hasErrors = false;
  errorMessages: Array<string>;
  allDisabled = false;
  required = false;

  private uniqueFormControls: FormControl[];
  private readonly form: NgForm;
  private readonly ngUnsubscribe: Subject<void> = new Subject();

  constructor(
    @Optional() @Inject(NgForm) protected ngForm: NgForm,
    @Optional() @Inject(ControlContainer) private readonly controlContainer: ControlContainer,
    private readonly validateMesssageService: ValidateMesssageService
  ) {
    this.form = ngForm || (controlContainer.formDirective as NgForm);
  }

  ngAfterContentInit(): void {
    if (this.inputComponentList.length === 0) {
      throw new Error('For content projection only type of CustomInputComponentAbstract allowed!');
    }

    this.useFieldset = this.inputComponentList.length > 1;

    this.uniqueFormControls = Array.from(new Set(this.inputComponentList.map(component => component.control)));

    this.prepareLabels();

    this.subscribeToErrors();

    this.subscribeToStatusChanges();
    this.setAllDisabledState();

    this.checkRequired();
  }

  /**
   * set up input/label relation
   * when fieldset used or no label provided activate screeenreader label
   */
  private prepareLabels(): void {
    if (this.inputComponentList.length === 1) {
      this.labelForId = this.inputComponentList.first.inputId;
    }

    if (this.useFieldset || !this.label) {
      const useScreenReaderLabelProp = 'useScreenReaderLabel';
      this.inputComponentList
        .filter(component => component.hasOwnProperty(useScreenReaderLabelProp))
        .forEach(component => {
          component[useScreenReaderLabelProp] = true;
        });
    }
  }

  /**
   * subscribe to all events where potential validation errors could occur
   */
  private subscribeToErrors(): void {
    const valueChanges$ = this.uniqueFormControls.map(control => control.valueChanges.pipe(distinctUntilChanged())),
      inputFieldFocusOut$ = this.inputComponentList
        .filter(component => component instanceof InputFieldComponent || component instanceof TextareaComponent)
        .map(component => component.focusOut);

    merge(this.form.ngSubmit, ...valueChanges$, ...inputFieldFocusOut$)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(this.setErrorMessages.bind(this));
  }

  /**
   * subscribe to contained components statusChanges
   */
  private subscribeToStatusChanges(): void {
    const statusChanges$ = this.uniqueFormControls.map(control => control.statusChanges.pipe(distinctUntilChanged()));
    merge(...statusChanges$)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(this.setAllDisabledState.bind(this));
  }

  /**
   * retrieve validation errors from contained form controls
   */
  private setErrorMessages(): void {
    const uniqueErrors = new Set();
    this.hasErrors = false;

    this.inputComponentList
      .filter(component => component.errors && !component.disabled && (component.touched || this.form.submitted))
      .forEach(component => {
        const errors = component.errors;
        Object.keys(errors).forEach(key => {
          // get error message from service and add it to the list
          uniqueErrors.add(this.validateMesssageService.getMessage(errors, key));
        });

        this.hasErrors = true;
      });

    if (this.hasErrors) {
      // display the messages
      this.errorMessages = Array.from(uniqueErrors);
    }
  }

  /**
   * set disabled state depending on each contained input control's disabled state
   */
  setAllDisabledState(): void {
    this.allDisabled = !this.uniqueFormControls.some(control => control.enabled);
  }

  /**
   * checks if some controls are required
   */
  checkRequired(): void {
    this.required = this.uniqueFormControls
      .map(ctrl => (ctrl.validator && ctrl.validator({} as AbstractControl)) || {})
      .some(validator => validator.required || validator.requiredTrue);
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
