import { Component, Input, HostBinding, OnInit, Inject, Optional, Host } from '@angular/core';
import { CustomInputComponentAbstract, MakeProviders } from './custom-input-component.abstract';
import { ControlContainer, NG_ASYNC_VALIDATORS, NG_VALIDATORS } from '@angular/forms';

let increment = 0;

@Component({
  selector: 'ui-core-radiobutton',
  templateUrl: './radiobutton.component.html',
  providers: MakeProviders(RadiobuttonComponent)
})
export class RadiobuttonComponent extends CustomInputComponentAbstract implements OnInit {
  @HostBinding('class') classList = 'custom-control custom-radio';
  // tslint:disable-next-line:no-input-rename
  @Input('value') radioValue: any;

  private readonly idPostFix = increment;

  constructor(
    @Optional() @Inject(NG_VALIDATORS) validators: Array<any>,
    @Optional() @Inject(NG_ASYNC_VALIDATORS) asyncValidators: Array<any>,
    @Optional() @Host() controlContainer: ControlContainer
  ) {
    super(validators, asyncValidators, controlContainer);
    increment++;
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.inputId += this.idPostFix;
  }
}
