import { Component, OnInit, Input, ElementRef, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'ui-core-button',
  templateUrl: './button.component.html'
})
export class ButtonComponent implements OnInit {
  buttonClass = 'btn';
  readonly buttonBaseClasses = {
    primary: 'primary',
    primaryInverse: 'primary-inverse',
    secondary: 'outline-secondary',
    tertiary: 'outline-tertiary',
    tertiaryAlt: 'outline-tertiary-alt',
    link: 'link',
    dropdown: 'dropdown'
  };

  @Input() variant = 'primary';
  @Input() type = 'button';
  @Input() disabled = false;
  @Input() name: string;
  @Input() buttonClasses: string;

  @Output() click = new EventEmitter<any>();

  constructor(private readonly element: ElementRef) {}

  ngOnInit() {
    const nativeElement = this.element.nativeElement;
    const iconBefore = nativeElement.querySelector('[iconBefore]');
    const iconAfter = nativeElement.querySelector('[iconAfter]');

    this.buttonClass += ` btn-${this.buttonBaseClasses[this.variant] || 'primary'} ${this.buttonClasses}`;

    if (iconBefore) {
      this.buttonClass += ' has-icon-before';
    }
    if (iconAfter) {
      this.buttonClass += ' has-icon-after';
    }
  }

  onClickButton(event) {
    this.click.emit(event);
    event.stopPropagation();
  }
}
